import React, { useState, useEffect } from 'react';

function ItemDetail({match}) {
  useEffect(() => {
    fetchItem();
    console.log("MATCH: ", match)
  }, []);

  const [unit, setUnit] = useState({
      images: {}
  });

  const fetchItem = async () => {
    const response = await fetch(`https://fortnite-api.theapinetwork.com/item/get?id=${match.params.id}`);

    const item = await response.json();

    console.log("RESP: ", response);
    console.log("ITEM: ", item);
    console.log("ITEM.DATA: ", item.data);
    console.log("ITEM.DATA.ITEM: ", item.data.item);
        
    setUnit(item.data.item);
  }

  return (
    <div>
        <h1>{unit.name}</h1>
        <img src={unit.images.background} width="200" height="300" alt="" />
        <h2>{unit.description}</h2>
    </div>
  );
}

export default ItemDetail;
